import React, { useState, useEffect } from 'react';
import {
  TextField,
  Button,
  Box,
  Typography,
  Tooltip
} from '@mui/material';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import DeleteIcon from '@mui/icons-material/Delete';
import DownloadIcon from '@mui/icons-material/Download';
import SaveIcon from '@mui/icons-material/Save';
import UploadIcon from '@mui/icons-material/Upload';
import EditIcon from '@mui/icons-material/Edit';
import IconButton from '@mui/material/IconButton';
import Checkbox from '@mui/material/Checkbox';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import Autocomplete from '@mui/material/Autocomplete';

import * as XLSX from 'xlsx';

interface DeductionItem {
  id: string;
  description: string;
  points: number;
  feedback: string;
}

interface Student {
  id: string;
  studentId: string;
  name: string;
  deductions: string[];
  score: number;
  feedback: string;
}

function App() {
  const [totalPoints, setTotalPoints] = useState(100);
  const [deductionItems, setDeductionItems] = useState<DeductionItem[]>([]);
  const [students, setStudents] = useState<Student[]>([]);
  const [selectedStudent, setSelectedStudent] = useState<string>('');

  // 学生追加用コンポーネント
  const [openStudentDialog, setOpenStudentDialog] = useState(false);
  const [newStudent, setNewStudent] = useState({
    studentId: '',
    name: ''
  });

  // 減点項目追加用のダイアログの状態
  const [openDeductionDialog, setOpenDeductionDialog] = useState(false);
  const [newDeduction, setNewDeduction] = useState({
    description: '',
    points: 0,
    feedback: ''
  });

  // 減点項目編集用のstate追加
  const [editingDeduction, setEditingDeduction] = useState<DeductionItem | null>(null);

  // 学生追加用ダイアログを開く
  const handleOpenStudentDialog = () => {
    setOpenStudentDialog(true);
    setNewStudent({
      studentId: '',
      name: ''
    });
  };

  // 学生追加用ダイアログを閉じる
  const handleCloseStudentDialog = () => {
    setOpenStudentDialog(false);
  };


  // LocalStorageからデータを読む
  useEffect(() => {
    console.log('useEffect');
    const savedDeductions = localStorage.getItem('deductionItems');
    const savedStudents = localStorage.getItem('students');

    if (savedDeductions) {
      setDeductionItems(JSON.parse(savedDeductions));
    }
    if (savedStudents) {
      setStudents(JSON.parse(savedStudents));
    }
  }, []);

  // 学生を追加する
  const handleAddStudent = () => {
    if (!newStudent.studentId || !newStudent.name) return;

    const newStudentData: Student = {
      id: `student-${Date.now()}`,
      studentId: newStudent.studentId,
      name: newStudent.name,
      deductions: [],
      score: totalPoints,
      feedback: ''
    };
    const newStudents = [...students, newStudentData];
    setStudents(newStudents);
    localStorage.setItem('students', JSON.stringify(newStudents));
    handleCloseStudentDialog();
  };

  // 選択中の学生を削除する
  const handleRemoveStudent = () => {
    if (!selectedStudent) return;
    const newStudents = students.filter(student => student.id !== selectedStudent);
    setStudents(newStudents);
    setSelectedStudent('');
    localStorage.setItem('students', JSON.stringify(newStudents));
  };

  // 減点項目の編集
  const handleEditDeduction = (item: DeductionItem) => {
    setEditingDeduction(item);
    setNewDeduction({
      description: item.description,
      points: item.points,
      feedback: item.feedback
    });
    setOpenDeductionDialog(true);
  };

  // 減点項目の削除
  const handleDeleteDeduction = (deductionId: string) => {
    const newDeductions = deductionItems.filter(item => item.id !== deductionId);
    setDeductionItems(newDeductions);
    localStorage.setItem('deductionItems', JSON.stringify(newDeductions));

    // 学生の採点項目からも削除
    const updatedStudents = students.map(student => ({
      ...student,
      deductions: student.deductions.filter(id => id !== deductionId)
    }));
    setStudents(updatedStudents);
    localStorage.setItem('students', JSON.stringify(updatedStudents));
  };

  // 減点項目を追加または更新
  const handleAddOrUpdateDeduction = () => {
    if (!newDeduction.description || !newDeduction.points) return;

    if (editingDeduction) {
      // 編集モード
      const updatedDeductions = deductionItems.map(item =>
        item.id === editingDeduction.id
          ? {
            ...item,
            description: newDeduction.description,
            points: newDeduction.points,
            feedback: newDeduction.feedback
          }
          : item
      );
      setDeductionItems(updatedDeductions);
      localStorage.setItem('deductionItems', JSON.stringify(updatedDeductions));

      // 学生のscoreとfeedbackの更新
      const updatedStudents = [];
      for (const student of students) {
        let newFeedback = '';
        updatedDeductions.forEach(deduction => {
          if (student.deductions.includes(deduction.id)) {
            newFeedback += `${deduction.feedback} (-${deduction.points} points)\n`;
          }
        });
        let newScore = totalPoints;
        updatedDeductions.forEach(deduction => {
          if (student.deductions.includes(deduction.id)) {
            newScore -= deduction.points;
          }
        });
        updatedStudents.push({
          ...student,
          feedback: newFeedback,
          score: newScore
        });
      }
      setStudents(updatedStudents);
      localStorage.setItem('students', JSON.stringify(updatedStudents));
    } else {
      // 新規追加モード
      const newItem: DeductionItem = {
        id: `deduction-${Date.now()}`,
        description: newDeduction.description,
        points: newDeduction.points,
        feedback: newDeduction.feedback
      };
      const newDeductions = [...deductionItems, newItem];
      setDeductionItems(newDeductions);
      localStorage.setItem('deductionItems', JSON.stringify(newDeductions));
    }

    setOpenDeductionDialog(false);
    setEditingDeduction(null);
  };

  // 減点項目のダイアログを閉じる
  const handleCloseDeductionDialog = () => {
    setOpenDeductionDialog(false);
    setEditingDeduction(null);
    setNewDeduction({
      description: '',
      points: 0,
      feedback: ''
    });
  };

  // 減点項目のチェック状態を変更
  const handleDeductionToggle = (deductionId: string) => {
    const currentStudent = students.find(s => s.id === selectedStudent);
    if (!currentStudent) return;

    const deduction = deductionItems.find(d => d.id === deductionId);
    if (!deduction) return;

    const newStudents = students.map(student => {
      if (student.id !== selectedStudent) return student;

      const hasDeduction = student.deductions.includes(deductionId);
      let newDeductions: string[];
      let newScore: number;
      let newFeedback: string;

      if (hasDeduction) {
        // チェックを外す場合
        newDeductions = student.deductions.filter(id => id !== deductionId);
      } else {
        // チェックを入れる場合
        newDeductions = [...student.deductions, deductionId];
      }

      // 採点の再計算
      newScore = totalPoints;
      newDeductions.forEach(id => {
        const deduction = deductionItems.find(d => d.id === id);
        if (deduction) {
          newScore -= deduction.points;
        }
      });

      newFeedback = '';
      deductionItems.forEach(deduction => {
        if (newDeductions.includes(deduction.id)) {
          newFeedback += `${deduction.feedback} (-${deduction.points} points)\n`;
        }
      });

      return {
        ...student,
        deductions: newDeductions,
        score: newScore,
        feedback: newFeedback
      };
    });

    setStudents(newStudents);
    localStorage.setItem('students', JSON.stringify(newStudents));
  }

  // Excelファイルを読み込む
  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      const data = new Uint8Array(e.target?.result as ArrayBuffer);;
      const workbook = XLSX.read(data, { type: 'array' });
      const sheetName = workbook.SheetNames[0];
      const worksheet = workbook.Sheets[sheetName];
      const jsonData = XLSX.utils.sheet_to_json(worksheet, { header: 1 }) as string[][];

      console.log(jsonData);

      // 1行目をスキップしたい場合は slice(1) を追加
      const newStudents = jsonData.map((row) => ({
        id: `student-${Date.now()}-${Math.random()}`,
        studentId: row[0]?.toString() || '',
        name: row[1]?.toString() || '',
        deductions: [],
        score: totalPoints,
        feedback: ''
      }));

      // 既存の学生リストと結合
      const updatedStudents = [...students, ...newStudents];
      setStudents(updatedStudents);
      localStorage.setItem('students', JSON.stringify(updatedStudents));
    };
    reader.readAsArrayBuffer(file);
  }

  // 採点結果をエクスポート
  const handleExportResults = () => {
    // エクスポートするデータの作成
    const exportData = students.map(student => ({
      '学籍番号': student.studentId,
      '氏名': student.name,
      '得点': student.score,
      '講評': student.feedback.trim()
    }));

    // ワークブックとワークシートの作成
    const workBook = XLSX.utils.book_new();
    const workSheet = XLSX.utils.json_to_sheet(exportData);

    // 列幅の設定
    const columnWidths = [
      { wch: 10 }, // 学籍番号
      { wch: 15 }, // 氏名
      { wch: 8 }, // 得点
      { wch: 50 } // 講評
    ];
    workSheet['!cols'] = columnWidths;

    // ワークブックにワークシートを追加
    XLSX.utils.book_append_sheet(workBook, workSheet, '採点結果');

    // ファイルとして保存
    const fileName = `採点結果_${new Date().toISOString().split('T')[0]}.xlsx`;
    XLSX.writeFile(workBook, fileName);
  };

  // このシステムの内部データ(deductionItems, students)を保存
  const handleSaveData = () => {
    const saveData = {
      deductionItems,
      students
    };

    const blob = new Blob([JSON.stringify(saveData, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `grading-data_${new Date().toISOString().split('T')[0]}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  }

  // このシステムの内部データ(deductionItems, students)を読み込む
  const handleLoadData = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const data = JSON.parse(e.target?.result as string);

        if (data.deductionItems && Array.isArray(data.deductionItems)) {
          setDeductionItems(data.deductionItems);
          localStorage.setItem('deductionItems', JSON.stringify(data.deductionItems));
        }

        if (data.students && Array.isArray(data.students)) {
          setStudents(data.students);
          localStorage.setItem('students', JSON.stringify(data.students));
        }
      } catch (error) {
        console.error('Invalid JSON file', error);
        alert('無効なJSONファイルです');
      }
    };
    reader.readAsText(file);
  };

  // 減点項目リストをインポートする
  const handleImportDeductions = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const data = JSON.parse(e.target?.result as string);

        if (!Array.isArray(data)) {
          alert('無効なJSONファイルです。配列形式である必要があります。');
          return;
        }

        const newDeductions: DeductionItem[] = data.map(item => ({
          id: `deduction-${Date.now()}-${Math.random()}`,
          description: item.description,
          points: item.point,
          feedback: item.feedback
        }));

        const uploadDeductions = [...deductionItems, ...newDeductions];
        setDeductionItems(uploadDeductions);
        localStorage.setItem('deductionItems', JSON.stringify(uploadDeductions));
      } catch (error) {
        console.error('Invalid JSON file', error);
        alert('無効なJSONファイルです');
      }
    };
    reader.readAsText(file);
  };

  // 減点項目リスト(JSON)をエクスポート
  const handleExportDeductions = () => {
    // idは抜いてエクスポート
    const exportData = deductionItems.map(item => ({
      description: item.description,
      points: item.points,
      feedback: item.feedback
    }));

    const blob = new Blob([JSON.stringify(exportData, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `deduction-items_${new Date().toISOString().split('T')[0]}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  }

  return (
    <div className="App">
      <header className="App-header">
        <Typography variant="h4" component="h1" gutterBottom>
          DSA Grading Tool
        </Typography>
      </header>
      <Box sx={{ p: 3, maxWidth: 800, margin: '0 auto' }}>
        <Box sx={{ mb: 3 }}>
          <TextField
            label="totalPoints"
            type="number"
            value={totalPoints}
            onChange={(e) => setTotalPoints(Number(e.target.value))}
            fullWidth
            variant="outlined"
          />
        </Box>

        <Box sx={{ mb: 2 }}>
          <Autocomplete
            // 現在選択されている値を指定
            value={students.find(student => student.id === selectedStudent) || null}
            // 選択値が変更されたときの処理
            onChange={(_, newValue) => setSelectedStudent(newValue?.id || '')}
            // 選択肢のリストを指定
            options={students}
            // 各選択肢の表示テキストをカスタマイズ
            getOptionLabel={(option) => `${option.name} (${option.studentId})`}
            // テキストフィールドの見た目をカスタマイズ
            renderInput={(params) => (
              <TextField
                {...params}
                label="学生を選択"
                variant="outlined"
              />
            )}
            // 値の比較方法をカスタマイズ
            // 学生オブジェクトのIDで比較
            isOptionEqualToValue={(option, value) => option.id === value.id}
          />
        </Box>

        <Box sx={{ display: 'flex', gap: 2, mb: 2 }}>
          <Button
            variant="contained"
            startIcon={<AddIcon />}
            onClick={handleOpenStudentDialog}
          >
            学生を追加
          </Button>
          <Button
            variant="contained"
            color="error"
            startIcon={<DeleteIcon />}
            onClick={handleRemoveStudent}
          >
            選択中の学生を削除
          </Button>
        </Box>

        {/* インポート関連のボタングループ */}
        <Box sx={{ display: 'flex', gap: 1 }}>
          <Tooltip title="名簿とは、1列目に学籍番号、2列目に名前が記載されたテーブルを指します" arrow>
            <Button
              variant="contained"
              component="label"
              color="secondary"
            >
              名簿をインポート(.xlsx, .xls, .csv)
              <input
                type="file"
                accept=".xlsx,.xls,.csv"
                hidden
                onChange={handleFileUpload}
              />
            </Button>
          </Tooltip>
          <Tooltip title="採点途中までのデータをセーブします" arrow>
            <Button
              variant="contained"
              color="secondary"
              startIcon={<SaveIcon />}
              onClick={handleSaveData}
              disabled={students.length === 0 && deductionItems.length === 0}
            >
              Save Data...
            </Button>
          </Tooltip>
          <Tooltip title="セーブした採点途中のデータをロードします" arrow>
            <Button
              variant="contained"
              component="label"
              color="secondary"
              startIcon={<UploadIcon />}
            >
              Load Data...
              <input
                type="file"
                accept=".json"
                hidden
                onChange={handleLoadData}
              />
            </Button>
          </Tooltip>
        </Box>

        <Typography variant="body2" color="text.secondary">
          学生数: {students.length}
        </Typography>
      </Box>

      {/* 学生追加ダイアログ */}
      <Dialog open={openStudentDialog} onClose={handleCloseStudentDialog}>
        <DialogTitle>学生を追加</DialogTitle>
        <DialogContent>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, pt: 2 }}>
            <TextField
              label="studentId"
              value={newStudent.studentId}
              onChange={(e) => setNewStudent({
                ...newStudent,
                studentId: e.target.value
              })}
              fullWidth
              variant="outlined"
            />
            <TextField
              label="name"
              value={newStudent.name}
              onChange={(e) => setNewStudent({
                ...newStudent,
                name: e.target.value
              })}
              fullWidth
              variant="outlined"
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseStudentDialog}>キャンセル</Button>
          <Button
            onClick={handleAddStudent}
            variant="contained"
            disabled={!newStudent.studentId || !newStudent.name}
          >
            追加
          </Button>
        </DialogActions>
      </Dialog>

      {/* 選択された学生の採点セクション */}
      {selectedStudent && (
        <>
          <Box sx={{ display: 'flex', gap: 1, mb: 2 }}>
            <Button
              variant="contained"
              startIcon={<AddIcon />}
              onClick={() => setOpenDeductionDialog(true)}
            >
              減点項目を追加
            </Button>
            <Tooltip title="減点項目のリストをJSONファイルからインポートします。{description, point, feedback}の3つのキーを持つ配列である必要があります。">
              <Button
                variant="contained"
                component="label"
                color="secondary"
                startIcon={<UploadIcon />}
              >
                減点項目リストをインポート
                <input
                  type="file"
                  accept=".json"
                  hidden
                  onChange={handleImportDeductions}
                />
              </Button>
            </Tooltip>
            <Tooltip title="減点項目のリストをJSONファイルとしてエクスポートします。">
              <Button
                variant="contained"
                color="secondary"
                startIcon={<DownloadIcon />}
                onClick={handleExportDeductions}
              >
                減点項目リストをエクスポート
              </Button>
            </Tooltip>
          </Box>
          <Box sx={{ mt: 4 }}>
            <Typography variant="h5" gutterBottom>
              Current Score: {students.find(s => s.id === selectedStudent)?.score.toFixed(2)} / {totalPoints.toFixed(2)}
            </Typography>
            <List>
              {deductionItems.map((item) => (
                <ListItem
                  key={item.id}
                  dense
                  secondaryAction={
                    <Box sx={{ display: 'flex', gap: 1 }}>
                      <IconButton edge="end" onClick={() => handleEditDeduction(item)}>
                        <EditIcon />
                      </IconButton>
                      <IconButton edge="end" onClick={() => handleDeleteDeduction(item.id)}>
                        <DeleteIcon />
                      </IconButton>
                    </Box>
                  }
                >
                  <Checkbox
                    edge="start"
                    checked={students.find(s => s.id === selectedStudent)?.deductions.includes(item.id) ?? false}
                    onChange={() => handleDeductionToggle(item.id)}
                  />
                  <ListItemText
                    primary={`${item.description} (-${item.points} points)`}
                  />
                </ListItem>
              ))}
            </List>

            <Typography variant="h6" gutterBottom sx={{ mt: 3 }}>
              Feedback(generated):
            </Typography>
            <TextField
              multiline
              rows={4}
              value={students.find(s => s.id === selectedStudent)?.feedback ?? ''}
              fullWidth
              variant="outlined"
              disabled
              sx={{
                "& .MuiInputBase-input.Mui-disabled": {
                  WebkitTextFillColor: "rgba(0, 0, 0, 1)"
                }
              }}
            />
          </Box>
        </>
      )}

      {/* 減点項目追加ダイアログ */}
      <Dialog open={openDeductionDialog} onClose={handleCloseDeductionDialog}>
        <DialogTitle>
          {editingDeduction ? '減点項目を編集' : '減点項目を追加'}
        </DialogTitle>
        <DialogContent>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, pt: 2 }}>
            <TextField
              label="description"
              value={newDeduction.description}
              onChange={(e) => setNewDeduction({
                ...newDeduction,
                description: e.target.value
              })}
              fullWidth
            />
            <TextField
              label="points"
              type="number"
              value={newDeduction.points}
              onChange={(e) => setNewDeduction({
                ...newDeduction,
                points: Number(e.target.value)
              })}
              fullWidth
            />
            <TextField
              label="feedback"
              multiline
              rows={3}
              value={newDeduction.feedback}
              onChange={(e) => setNewDeduction({
                ...newDeduction,
                feedback: e.target.value
              })}
              fullWidth
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDeductionDialog}>キャンセル</Button>
          <Button
            onClick={handleAddOrUpdateDeduction}
            variant="contained"
            disabled={!newDeduction.description || !newDeduction.points || !newDeduction.feedback}
          >
            {editingDeduction ? '更新' : '追加'}
          </Button>
        </DialogActions>
      </Dialog>

      {/* エクスポート関連のボタングループ */}
      <Box sx={{ display: 'flex', gap: 1 }}>
        <Button
          variant="contained"
          color="success"
          startIcon={<DownloadIcon />}
          onClick={handleExportResults}
          disabled={students.length === 0}
        >
          採点結果をエクスポート(.xlsx)
        </Button>
      </Box>
    </div>
  );
}

export default App;
